.header {
  background-color: transparent;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  padding-top: 20px;
  padding-bottom: 20px;
  z-index: 10;
}

.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
}

@media(max-width: 768px) {
  .header {
    padding-top: 10px;
  }
}